(function() {
  angular.module("menu", [])
    .provider("MenuService", menuProvider);

      var menuUrl = "/rs/navigation/loadMenu";
      var baseUrl = '/api';

      function menuProvider() {

        var provider = {
          setMenuUrl : setMenuUrl,
          setBaseUrl : setBaseUrl,
          $get : menuService
        };

        return provider;

        function setMenuUrl(url) {
          "use strict";
          menuUrl = url;
        };

        function setBaseUrl(url) {
          "use strict";
          baseUrl = url;
        };

        menuService.$inject = ['$http', '$q'];

        function menuService($http, $q) {
          "use strict";
          var service = {
            loadMenu : loadMenu
          };

          return service;

          function loadMenu() {
            var deferred = $q.defer();
            return $http.get(baseUrl + menuUrl)
              .then(function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data);
                    return deferred.promise;
                  }
                  else {
                    deferred.reject(data.message);
                    return deferred.promise;
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });
          }
        };

    };

})();

(function() {
  angular.module("message-handler", ["pascalprecht.translate"])
    .factory("MessageHandlerService", messageHandlerService);

  messageHandlerService.$inject = ['$rootScope', '$translate', '$q'];

  function messageHandlerService($rootScope, $translate, $q) {


        var service = {
          displayErrorMessage : displayErrorMessage,
          displayWarningMessage : displayWarningMessage,
          displayInfoMessage : displayInfoMessage,
          cleanWarnings : cleanWarnings,
          cleanInfos : cleanInfos,
          cleanErrors : cleanErrors
        };

        return service;

        function displayErrorMessage(message) {
          $rootScope.error = true;
          $translate(message).then(function (translation) {
            if (angular.isDefined(translation) && translation != null)
              $rootScope.message = translation;
            else
              $rootScope.message = message;
          });
        };

        function cleanErrors() {
          $rootScope.error = false;
          $rootScope.message = "";
        };

        function cleanInfos() {
          $rootScope.information = false;
          $rootScope.message = "";
        };

        function cleanWarnings() {
          $rootScope.warning = false;
          $rootScope.message = "";
        };

        function displayInfoMessage(message) {
          $rootScope.information = true;
          $translate(message).then(function (translation) {
            if (angular.isDefined(translation) && translation != null)
              $rootScope.message = translation;
            else
              $rootScope.message = message;
          });
        };

        function displayWarningMessage(message) {
          $rootScope.warning = true;
          $translate(message).then(function (translation) {
            if (angular.isDefined(translation) && translation != null)
              $rootScope.message = translation;
            else
              $rootScope.message = message;
          });
        }
      };

})();

