(function() {
  angular.module("entity", ["settings", "attachements"]);

  angular.module("entity")
    .factory("EntityService", entityService);

      function entityService($http, $q, AttachementService, Settings)
      {
        var servicePath = "";

        var service = {
          setServicePath : setServicePath,
          getServicePath : getServicePath,
          create : create,
          view : view,
          search : search,
          searchAll : searchAll,
          edit : edit,
          cancelEdit : cancelEdit,
          save : save,
          delete : deleteEntity
        };

        return service;

        function setServicePath(path) {
            servicePath = path;
            if (servicePath.startsWith("/"))
              servicePath = servicePath.substring(1);

            if (servicePath.endsWith("/"))
              servicePath = servicePath.substring(0, servicePath.length - 1);
        };

        function getServicePath() {
            return servicePath;
        };

        function create(entity) {

            var deferred = $q.defer();
            var uuid = AttachementService.getUUID();

            return $http.post(Settings.base + "/rs/" + servicePath + "/create/" + uuid, entity)
              .then(function (resp) {
                var data = resp.data;
                if (data.status == 0) {
                  deferred.resolve(data.object);
                  return deferred.promise;
                }
                else {
                  if (typeof(resp.data) == 'object') {
                    deferred.reject(resp.data.message);
                    return deferred.promise;
                  }
                  else {
                    deferred.reject(resp.statusText);
                    return deferred.promise;
                  }
                }
              }, function (resp) {
                deferred.reject(resp.statusText);
                return deferred.promise;
              });
        };

        function view(id) {

            var deferred = $q.defer();
            return $http.get(Settings.base + "/rs/" + servicePath + "/" + id)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data.object);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });
        };

        function search(currentPage, pageSize, criteriaEntity) {

            var deferred = $q.defer();
            if (typeof(currentPage) == 'undefined' || currentPage == null)
              currentPage = 1;
            if (typeof(pageSize) == 'undefined' || pageSize == null)
              pageSize = 10;

            var myUrl = Settings.base + '/rs/' + servicePath + '/search/page/' + currentPage + '/' + pageSize;
            var data = {};

            for (var member in criteriaEntity) {
              if (angular.isDefined(criteriaEntity[member]) && criteriaEntity[member] != null)
                data[member] = criteriaEntity[member];
            }

            var req = {
              method: 'POST',
              url: myUrl,
              data: data
            };

            return $http(req)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });

        };

        function searchAll(criteriaEntity) {

            var deferred = $q.defer();

            var myUrl = Settings.base + '/rs/' + servicePath + '/search';
            var data = {};

            for (var member in criteriaEntity) {
              if (angular.isDefined(criteriaEntity[member]) && criteriaEntity[member] != null)
                data[member] = criteriaEntity[member];
            }

            var req = {};
            if (!jQuery.isEmptyObject(data)) {
              req = {
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
                method: 'POST',
                url: myUrl,
                data: data
              };
            }
            else {
              req = {
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
                method: 'POST',
                url: myUrl
              };
            }

            return $http.post(myUrl, data)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });

        };

        function edit(id) {

            var deferred = $q.defer();
            return $http.get(Settings.base + "/rs/" + servicePath + "/edit/" + id)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data.object);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });
        };

        function cancelEdit(id) {

            var deferred = $q.defer();
            return $http.get(Settings.base + "/rs/" + servicePath + "/cancelEdit/" + id)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data.object);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });
        };

        function save(entity) {

            var deferred = $q.defer();
            var uuid = AttachementService.getUUID();

            var myUrl = Settings.base + '/rs/' + servicePath + '/save/' + uuid;
            return $http.post(myUrl, entity)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data.object);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });
        };

        function deleteEntity(id) {

            var deferred = $q.defer();
            return $http.get(Settings.base + "/rs/" + servicePath + "/delete/" + id)
              .then(
                function (resp) {
                  var data = resp.data;
                  if (data.status == 0) {
                    deferred.resolve(data.status);
                    return deferred.promise;
                  }
                  else {
                    if (typeof(resp.data) == 'object') {
                      deferred.reject(resp.data.message);
                      return deferred.promise;
                    }
                    else {
                      deferred.reject(resp.statusText);
                      return deferred.promise;
                    }
                  }
                }
                , function (resp) {
                  deferred.reject(resp.statusText);
                  return deferred.promise;
                });
        };
      };

      entityService.$inject =  ['$http', '$q', 'AttachementService', 'Settings'];

})();

(function() {
  angular.module("crud", []);
  angular.module("crud").factory("CrudService", crudService);

    function crudService($rootScope) {

      var INIT = "init";
      var ADD = "add";
      var EDIT = "edit";
      var DETAIL = "detail";
      var SEARCH = "search";

      var currentMode = INIT;

      var createEnabled = true;
      var addEnabled = false;
      var editEnabled = false;
      var saveEnabled = false;
      var deleteEnabled = false;
      var cancelEnabled = false;
      var searchEnabled = false;
      var backEnabled = false;
      var disable = false;

      var service = {
        setMode : setMode,
        resetObject : resetObject,
        initMode : initMode,
        createMode : createMode,
        addMode : addMode,
        editMode : editMode,
        saveMode : saveMode,
        resetObject : resetObject,
        deleteMode : deleteMode,
        detailMode : detailMode,
        searchMode : searchMode,
        isCreateEnabled : isCreateEnabled,
        isEditEnabled : isEditEnabled,
        isSaveEnabled : isSaveEnabled,
        isAddEnabled : isAddEnabled,
        isDeleteEnabled : isDeleteEnabled,
        isCancelEnabled : isCancelEnabled,
        isBackEnabled : isBackEnabled,
        isDisable : isDisable,
        getNumPages : getNumPages,
        getCurrentMode : getCurrentMode
      };

      return service;

      function setMode(mode) {
        if (mode == INIT) {
          createEnabled = true;
          addEnabled = false;
          editEnabled = false;
          saveEnabled = false;
          deleteEnabled = false;
          cancelEnabled = false;
          searchEnabled = false;
          disable = false;
          backEnabled = false;
          currentMode = mode;
          $rootScope.screenMode = mode;
        }
        if (mode == DETAIL) {
          createEnabled = false;
          addEnabled = true;
          editEnabled = true;
          saveEnabled = false;
          deleteEnabled = true;
          cancelEnabled = false;
          searchEnabled = false;
          backEnabled = true;
          disable = true;
          currentMode = mode;
          $rootScope.screenMode = mode;
        }
        if (mode == EDIT) {
          createEnabled = false;
          addEnabled = false;
          editEnabled = false;
          saveEnabled = true;
          deleteEnabled = false;
          cancelEnabled = true;
          searchEnabled = false;
          backEnabled = true;
          disable = false;
          currentMode = mode;
          $rootScope.screenMode = mode;
        }
        if (mode == ADD) {
          createEnabled = true;
          addEnabled = false;
          editEnabled = false;
          saveEnabled = false;
          deleteEnabled = false;
          cancelEnabled = false;
          searchEnabled = false;
          backEnabled = true;
          disable = false;
          currentMode = mode;
          $rootScope.screenMode = mode;
        }
        if (mode == SEARCH) {
          createEnabled = false;
          addEnabled = false;
          editEnabled = false;
          saveEnabled = false;
          deleteEnabled = false;
          cancelEnabled = false;
          searchEnabled = true;
          backEnabled = false;
          disable = false;
          currentMode = mode;
          $rootScope.screenMode = mode;
        }
      };
      function resetObject(obj) {
        for (var v in obj) {
          v = null;
        }
      };
      function initMode() {
        return INIT;
      };
      function createMode() {
        return DETAIL;
      };
      function addMode() {
        return ADD;
      };
      function editMode() {
        return EDIT;
      };
      function saveMode() {
        return DETAIL;
      };
      function deleteMode() {
        return INIT;
      };
      function detailMode() {
        return DETAIL;
      };
      function searchMode() {
        return DETAIL;
      };
      function isCreateEnabled() {
        return createEnabled;
      };
      function isEditEnabled() {
        return editEnabled;
      };
      function isSaveEnabled() {
        return saveEnabled;
      };
      function isAddEnabled() {
        return addEnabled;
      };
      function isDeleteEnabled() {
        return deleteEnabled;
      };
      function isCancelEnabled() {
        return cancelEnabled;
      };
      function isBackEnabled() {
        return backEnabled;
      };
      function isDisable() {
        return disable;
      };
      function getNumPages(count, psize) {
        var reste = count % psize;
        var diviseur = count - reste;
        return ((diviseur / psize) + 1);
      };
      function getCurrentMode() {
        return currentMode;
      };
      function reload(scope) {
        scope.createEnabled = createEnabled;
        scope.addEnabled = addEnabled;
        scope.cancelEnabled = cancelEnabled;
        scope.editEnabled = editEnabled;
        scope.saveEnabled = saveEnabled;
        scope.deleteEnabled = deleteEnabled;
        scope.backEnabled = backEnabled;
        scope.disable = disable;
        scope.mode = currentMode;
      };

    };

    crudService.$inject = ['$rootScope'];

})();

